import Swiper from '../assets/libs/swiper/swiper-bundle.esm.browser';

'use strict';
document.addEventListener("DOMContentLoaded", () => {
    // SLIDER
      
  const swiper = new Swiper('.slider', {
    loop: true,
    navigation: {
      nextEl: '.slider__button-right',
      prevEl: '.slider__button-left',
    },
    speed: 600,
    autoplay: {
      delay: 5000,
    },
      slidesPerView: 'auto',
      centeredSlides: true,
      loop: true
    });

    // BORDER BOTTOM APPEARANCE

    const allSections = document.querySelectorAll('section');
          allSections.forEach(section => {
            section.addEventListener('mouseenter', () => {
                const lightTitle = section.querySelector('.title_subtitle-light '),
                      darkTitle = section.querySelector('.title_subtitle-dark ');

                if (lightTitle) {
                    lightTitle.classList.add('title-border-bottom');
                } else if (darkTitle) {
                    darkTitle.classList.add('title-border-bottom');
                }
            }, {passive: true})
          })  
});